	module.exports = {"data":{"assignments":{"id":"YXNzaWdubWVudDo3MTE=","postId":711,"title":"Learn about \"DeepTriage\" to see how we can incorporate into Call Bell, Attest, Watchtower, etc.","description":"For a given software bug report, identifying an appropriate developer who could potentially fix the bug is the primary task of a bug triaging process. A bug title (summary) and a detailed description is present in most of the bug tracking systems. Automatic bug triaging algorithm can be formulated as a classification problem, which takes the bug title and description as the input, mapping it to one of the available developers (class labels). The major challenge is that the bug description usually contains a combination of free unstructured text, code snippets, and stack trace making the input data highly noisy.\r\n\r\nThese same techniques should be used by Citus Health Call Bell to do auto-triage based on questions -- perhaps finding the right user to assign a chat message to based on its content. Same for Watchtower logs and feedback and Opsfolio Attest's answers to questions.","author":{"name":"Shahid Shah","email":"shahid.shah@netspective.com"},"expiryDate":null,"actualUrl":"http://bugtriage.mybluemix.net/","terms":[{"categoryId":"416","name":"Architecture and Engineering"},{"categoryId":"380","name":"Citus Health Engineering"},{"categoryId":"411","name":"Netspective Attest"},{"categoryId":"129","name":"Netspective Communications"},{"categoryId":"422","name":"Netspective Opsfolio"}]}},"pathContext":{"id":"YXNzaWdubWVudDo3MTE="}}

